import React, { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import CloseIcon from '@mui/icons-material/Close';
import Grid from '@mui/material/Grid';
import moment from 'moment';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import './Contract.scss';
import { TextField } from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import InputAdornment from '@mui/material/InputAdornment';

export default function ContractDialog(props) {
  const { open, item, handleCloseDialog, setItem } = props;
  const formik = useFormik({
    initialValues: {
      code: item?.employee ? item.code : '',
      nameLeader: item?.employee ? item.nameLeader : '',
      postionLeader: item?.employee ? item.postionLeader : '',
      signingDate: item?.employee ? item.signingDate : null,
      contractEffect: item?.employee ? item.contractEffect : null,
      basicSalary: item?.employee ? item.basicSalary : '',
      hourlyRate: item?.employee ? item.hourlyRate : '',
    },
    enableReinitialize: true,
    validateOnChange: false,
    validateOnBlur: false,
    validationSchema: Yup.object({
      code: Yup.string()
        .matches(/^MaH[0-9]{4}$/, 'M hp ng cha ng format VD:(MaH9999)')
        .required('Vui lng nhp trng ny'),
      nameLeader: Yup.string().required('Vui lng nhp trng ny'),
      postionLeader: Yup.string().required('Vui lng nhp trng ny'),
      signingDate: Yup.date()
        .nullable()
        .typeError('Sai nh dng ngy!')
        .required('Vui lng nhp trng ny'),
      contractEffect: Yup.date()
        .nullable()
        .typeError('Sai nh dng ngy!')
        .required('Vui lng nhp trng ny'),
      basicSalary: Yup.number()
        .typeError('S lng phi l s!')
        .required('Vui lng nhp trng ny'),
      hourlyRate: Yup.number()
        .typeError('S lng phi l s!')
        .required('Vui lng nhp trng ny'),
    }),
    onSubmit: (values) => {
      values.employee = item;
      values.status = 4;
      values.coefficientSalary = 1.5;
      setItem({ ...item, contract: values, status: 12 });
    },
  });

  const handlePrint = () => {
    var oldstr = document.body.innerHTML;
    document.body.innerHTML = document.getElementById('contract').innerHTML;
    window.print();
    window.location.reload();
    document.body.innerHTML = oldstr;
  };

  return (
    <Dialog open={open} fullWidth maxWidth={'md'}>
      <DialogTitle style={{ marginBlockEnd: 0, padding: '16px 24px 0' }}>
        <Box className="icon-close" onClick={handleCloseDialog}>
          <IconButton color="error">
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>
      <form onSubmit={formik.handleSubmit} onError={(errors) => console.log(errors)}>
        <DialogContent style={{ padding: '0 20px' }} id="contract">
          <Grid
            container
            spacing={2}
            style={{ fontFamily: '"Times New Roman", Times, serif', fontSize: '18px' }}
          >
            <Grid item container xs={12}>
              <Grid
                item
                container
                xs={12}
                style={{ fontSize: '25px', textTransform: "uppercase'", fontWeight: 'bold' }}
                alignItems="center"
                justifyContent="center"
              >
                Cng ha x hi ch ngha Vit Nam
              </Grid>
              <Grid
                item
                container
                xs={12}
                style={{ fontSize: '22px' }}
                alignItems="center"
                justifyContent="center"
              >
                c lp - T do - Hnh phc
              </Grid>
              <Grid
                item
                container
                xs={12}
                style={{ fontSize: '22px', fontWeight: 'bold' }}
                alignItems="center"
                justifyContent="center"
              >
                *****
              </Grid>
              <Grid item container xs={10} spacing={1} className="pd-60">
                <Grid item className="mr-10">
                  M hp ng:
                </Grid>
                <Grid item xs={3} md={3}>
                  <TextField
                    variant="standard"
                    fullWidth
                    name="code"
                    value={formik.values?.code}
                    onChange={formik.handleChange}
                    InputProps={{
                      readOnly: item?.employee,
                    }}
                    error={formik.errors.code && formik.touched.code}
                    helperText={formik.errors.code}
                  />
                </Grid>
              </Grid>
            </Grid>
            <Grid item container xs={12}>
              <Grid
                item
                container
                xs={12}
                style={{
                  fontSize: '30px',
                  textTransform: 'uppercase',
                  fontWeight: 'bold',
                }}
                alignItems="center"
                justifyContent="center"
              >
                {' '}
                Hp ng lao ng
              </Grid>
              <Grid item container xs={2}></Grid>
              <Grid
                item
                container
                xs={8}
                style={{
                  marginBottom: 20,
                  textAlign: 'center',
                }}
                alignItems="center"
                justifyContent="center"
              >
                (Ban hnh theo Thng t s 21/2003/TT-BLTBXH ngy 22/9/2003 ca B Lao ng 
                Thng binh v X hi)
              </Grid>
            </Grid>
            <Grid item container xs={12} className="pd-60" spacing={1} alignItems="center">
              <Grid item xs={12} md={12}>
                Chng ti, mt bn l:
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10" style={{ padding: '2px 0' }}>
                  ng/B:
                </Grid>
                <Grid item xs={6} md={6}>
                  <TextField
                    variant="standard"
                    fullWidth
                    className="name"
                    name="nameLeader"
                    value={formik.values?.nameLeader}
                    onChange={formik.handleChange}
                    InputProps={{
                      readOnly: item?.employee,
                    }}
                    error={formik.errors.nameLeader && formik.touched.nameLeader}
                    helperText={formik.errors.nameLeader}
                  />
                </Grid>
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  Chc v:
                </Grid>
                <Grid item xs={6} md={6}>
                  <TextField
                    variant="standard"
                    fullWidth
                    name="postionLeader"
                    value={formik.values?.postionLeader}
                    onChange={formik.handleChange}
                    InputProps={{
                      readOnly: item?.employee,
                    }}
                    error={formik.errors.postionLeader && formik.touched.postionLeader}
                    helperText={formik.errors.postionLeader}
                  />
                </Grid>
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  i din cho cng ty:
                </Grid>
                <Grid item xs={6} md={6} style={{ textTransform: 'uppercase' }}>
                  Cng ty TNHH Oceantech
                </Grid>
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  a ch cng ty:
                </Grid>
                <Grid item xs={9} md={9}>
                  S 467 Nguyn Tri, Thanh Xun Nam, Thanh Xun, H Ni
                </Grid>
              </Grid>
              <Grid item xs={12} md={12}>
                V mt bn l:
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  ng/B:
                </Grid>
                <Grid item xs={6} md={6} style={{ textTransform: 'uppercase', fontWeight: 'bold' }}>
                  {item?.employee ? item?.employee?.fullName : item?.fullName}
                </Grid>
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  S CCCD:
                </Grid>
                <Grid item xs={2} md={2} style={{ textTransform: 'uppercase', fontWeight: 'bold' }}>
                  {item?.employee ? item?.employee?.numberIdentityCard : item?.numberIdentityCard}
                </Grid>
                <Grid item className="mr-10">
                  Ngy cp:
                </Grid>
                <Grid item xs={2} md={2}>
                  {item?.employee
                    ? moment(item?.employee?.issuedDateIdentityCard).format('DD/MM/YYYY')
                    : moment(item?.issuedDateIdentityCard).format('DD/MM/YYYY')}
                </Grid>
                <Grid item className="mr-10">
                  Ni cp:
                </Grid>
                <Grid item xs={3} md={3}>
                  {item?.employee
                    ? item?.employee?.placeOfGrantIdentityCard
                    : item?.placeOfGrantIdentityCard}
                </Grid>
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  a ch:
                </Grid>
                <Grid item xs={10} md={10}>
                  {item?.employee ? item?.employee?.address : item?.address}
                </Grid>
              </Grid>
              <Grid item container xs={12} md={12}>
                Tha thun k kt hp ng lao ng v cam kt lm ng nhng iu khon sau y:
              </Grid>
              <Grid item container xs={12} md={12}>
                <Grid item className="mr-10">
                  <ul className="dashed">
                    <span style={{ fontWeight: 'bold' }}>
                      iu 1: Thi hn v cng vic hp ng
                    </span>
                    <li>
                      <span className="mr-10">Bt u t</span>
                      <LocalizationProvider dateAdapter={AdapterDayjs} fullWidth>
                        <DatePicker
                          name="signingDate"
                          inputFormat="DD/MM/YYYY"
                          value={formik.values?.signingDate || null}
                          onChange={(value) => {
                            if (value) {
                              formik.setFieldValue('signingDate', new Date(value));
                            }
                          }}
                          readOnly={item?.employee}
                          renderInput={(params) => {
                            return (
                              <TextField
                                {...params}
                                format="DD/MM/YYYY"
                                type="date"
                                variant="standard"
                                error={formik.errors.signingDate && formik.touched.signingDate}
                                helperText={formik.errors.signingDate}
                              />
                            );
                          }}
                        />
                      </LocalizationProvider>
                      <span className="ml-10  mr-10">n ht</span>
                      <LocalizationProvider dateAdapter={AdapterDayjs} fullWidth>
                        <DatePicker
                          name="contractEffect"
                          inputFormat="DD/MM/YYYY"
                          value={formik.values?.contractEffect || null}
                          onChange={(value) => {
                            if (value) {
                              formik.setFieldValue('contractEffect', new Date(value));
                            }
                          }}
                          readOnly={item?.employee}
                          renderInput={(params) => {
                            return (
                              <TextField
                                {...params}
                                format="DD/MM/YYYY"
                                type="date"
                                variant="standard"
                                error={
                                  formik.errors.contractEffect && formik.touched.contractEffect
                                }
                                helperText={formik.errors.contractEffect}
                              />
                            );
                          }}
                        />
                      </LocalizationProvider>
                    </li>
                    <li>a im lm vic ti Cng ty TNHH Oceantech</li>
                    <li>
                      <span className="mr-10">Chc v:</span>
                      {item?.employee
                        ? item?.employee?.positions[0]?.name
                        : item?.positions[0]?.name}
                    </li>
                    <li>
                      <span className="mr-10">Phng ban:</span>
                      {item?.employee ? item?.employee?.department?.name : item?.department?.name}
                    </li>
                  </ul>
                </Grid>
                <Grid item className="mr-10" style={{ marginBottom: 20 }}>
                  <ul className="dashed">
                    <span style={{ fontWeight: 'bold' }}>iu 2: Ch  lm vic</span>
                    <li>
                      Thi gi lm vic 08 gi/ngy, 44 gi/tun. Sn sng lm thm gi vo ngy
                      ngh tu cng vic.
                    </li>
                    <li>
                      c cp pht nhng dng c lm vic gm: Ty theo yu cu cng vic, ngi
                      lao ng c cp pht dng c cn thit.
                    </li>
                    <li>
                      Sau khi k hp ng, ngi lao ng s th vic vi mc lng 80% mc lng c
                      bn
                    </li>
                  </ul>
                </Grid>
                <Grid item className="mr-10">
                  <ol className="number">
                    <span style={{ fontWeight: 'bold' }}>
                      iu 3: Ngha v v quyn li ca ngi lao ng
                    </span>
                    <li style={{ fontWeight: 'bold' }}>
                      <span> Quyn li</span>
                    </li>
                    <ul className="dashed">
                      <li>Phng tin i li lm vic : T tc.</li>
                      <li>
                        <span className="mr-10"> Mc lng c bn:</span>
                        <TextField
                          variant="standard"
                          name="basicSalary"
                          value={
                            formik.values?.basicSalary
                              ? formik.values?.basicSalary
                                  .toString()
                                  .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',')
                              : ''
                          }
                          onChange={(event) => {
                            formik.setFieldValue(
                              'basicSalary',
                              event.target.value.replace(/,/g, '')
                            );
                          }}
                          InputProps={{
                            maxLength: 10,
                            endAdornment: <InputAdornment position="end">VND</InputAdornment>,
                            readOnly: item?.employee,
                          }}
                          error={formik.errors.basicSalary && formik.touched.basicSalary}
                          helperText={formik.errors.basicSalary}
                        />
                      </li>
                      <li>
                        <span className="mr-10">S tin tnh cho 1h lm thm:</span>
                        <TextField
                          variant="standard"
                          name="hourlyRate"
                          value={
                            formik.values?.hourlyRate
                              ? formik.values?.hourlyRate
                                  .toString()
                                  .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',')
                              : ''
                          }
                          onChange={(event) => {
                            formik.setFieldValue(
                              'hourlyRate',
                              event.target.value.replace(/,/g, '')
                            );
                          }}
                          InputProps={{
                            maxLength: 10,
                            endAdornment: <InputAdornment position="end">VND</InputAdornment>,
                            readOnly: item?.employee,
                          }}
                          error={formik.errors.hourlyRate && formik.touched.hourlyRate}
                          helperText={formik.errors.hourlyRate}
                        />
                      </li>
                      <li>Hnh thc tr lng : Tin mt hoc chuyn khon.</li>
                      <li>c tr lng : 01 ln vo ngy 01 n 05 hng thng.</li>
                      <li>Tin thng : Theo quy ch ca Cng ty.</li>
                      <li>Ch  nng lng : Theo quy ch ca Cng ty.</li>
                      <li>
                        Ch  ngh ngi (ngh hng tun, php nm, l tt...): Theo ch  quy nh
                        ca Nh nc v quy ch ca Cng ty.
                      </li>
                      <li>
                        Bo him x hi, bo him y t, bo him tht nghip: Theo quy nh ca Nh
                        nc;
                      </li>
                      <li>
                        Ch  o to: Theo quy ch ca Cng ty. Trong trng hp nhn vin c c
                        i o to th nhn vin  phi hon thnh kho hc ng thi hn, phi cam
                        kt s phc v lu di cho Cng ty sau khi kt thc kho hc v c hng
                        nguyn lng trong thi gian hc, cc quyn li khc nh ngi i lm. Nu
                        sau khi kt thc kha o to m nhn vin khng tip tc hp tc vi Cng
                        ty th nhn vin phi hon tr li ph o to v cc khon ch   c
                        nhn trong thi gian o to.
                      </li>
                      <li>Nhng tha thun khc: Theo quy ch ca Cng ty.</li>
                    </ul>
                    <li style={{ fontWeight: 'bold' }}>
                      <span> Ngha v:</span>
                    </li>
                    <ul className="dashed">
                      <li>Hon thnh nhng cng vic  cam kt trong hp ng lao ng.</li>
                      <li>
                        Chp hnh lnh iu hnh sn xut - kinh doanh, ni quy k lut lao ng, an
                        ton lao ng ...
                      </li>
                      <li>Bi thng vi phm v vt cht: theo quy nh ca Cng ty.</li>
                    </ul>
                  </ol>
                </Grid>
                <Grid item className="mr-10">
                  <ol className="number">
                    <span style={{ fontWeight: 'bold' }}>
                      iu 4: Ngha v v quyn hn ca ngi s dng lao ng
                    </span>
                    <li style={{ fontWeight: 'bold' }}>
                      <span> Ngha v:</span>
                    </li>
                    <ul className="dashed">
                      <li>
                        Bo m vic lm v thc hin y  nhng iu  cam kt trong hp ng
                        lao ng.
                      </li>
                      <li>
                        Thanh ton y , ng thi hn cc ch  v quyn li cho ngi lao ng
                        theo hp ng lao ng, tha c lao ng tp th (nu c).
                      </li>
                    </ul>
                    <li style={{ fontWeight: 'bold' }}>
                      <span> Quyn hn:</span>
                    </li>
                    <ul className="dashed">
                      <li>
                        iu hnh ngi lao ng hon thnh cng vic theo hp ng (b tr, iu
                        chuyn, tm ngng vic).
                      </li>
                      <li>
                        Tm hon, chm dt hp ng lao ng, k lut ngi lao ng theo quy nh
                        ca php lut, tha c lao ng tp th (nu c) v ni quy lao ng ca
                        doanh nghip.
                      </li>
                    </ul>
                  </ol>
                </Grid>
                <Grid item className="mr-10">
                  <ul className="dashed">
                    <span style={{ fontWeight: 'bold' }}>iu 5: iu khon thi hnh</span>
                    <li>
                      Nhng vn  v lao ng khng ghi trong hp ng lao ng ny th p dng qui
                      nh ca tha c tp th, trng hp cha c tha c tp th th p dng quy
                      nh ca php lut lao ng.
                    </li>
                    <li>
                      Hp ng lao ng c lm thnh 02 bn c gi tr ngang nhau, mi bn gi 01
                      bn v c hiu lc t{' '}
                      {formik.values?.signingDate
                        ? moment(formik.values?.signingDate).format('DD/MM/YYYY')
                        : '.../.../...'}
                      . Khi hai bn k kt ph lc hp ng lao ng th ni dung ca ph lc hp
                      ng lao ng cng c gi tr nh cc ni dung ca bn hp ng lao ng ny.
                    </li>
                  </ul>
                </Grid>
              </Grid>
            </Grid>
            <Grid
              item
              container
              xs={12}
              className="pd-60"
              spacing={1}
              alignItems="center"
              style={{ marginBottom: 10 }}
            >
              <Grid item xs={7}></Grid>
              <Grid item xs={5} style={{ marginTop: '10px' }}>
                H Ni, ngy {new Date(formik.values.signingDate).getDate()} thng{' '}
                {new Date(formik.values?.signingDate).getMonth() + 1} nm{' '}
                {new Date(formik.values?.signingDate).getFullYear()}
              </Grid>
            </Grid>
            <Grid
              item
              container
              xs={12}
              className="pd-60"
              spacing={0.5}
              alignItems="center"
              style={{ marginBottom: 100 }}
            >
              <Grid item xs={2}></Grid>
              <Grid item xs={4} style={{ fontWeight: 'bold' }}>
                Ngi lao ng
              </Grid>
              <Grid item xs={2}></Grid>
              <Grid item xs={4} style={{ fontWeight: 'bold' }}>
                Ngi s dng lao ng
              </Grid>
              <Grid item xs={2}></Grid>
              <Grid item xs={4} style={{ fontStyle: 'italic' }}>
                (K, ghi r h tn)
              </Grid>
              <Grid item xs={1}></Grid>
              <Grid item xs={5} style={{ fontStyle: 'italic', textAlign: 'center' }}>
                (K, ghi r h tn, ng du)
              </Grid>
            </Grid>
            <Grid
              item
              container
              xs={12}
              className="pd-60"
              spacing={0.5}
              alignItems="center"
              justifyContent="center"
              style={{ marginBottom: 20 }}
            >
              <Grid item xs={2}></Grid>
              <Grid item xs={4} style={{ fontWeight: 'bold' }}>
                {item?.employee ? item?.employee?.fullName : item?.fullName}
              </Grid>
              <Grid item xs={2}></Grid>
              <Grid item xs={4} style={{ fontWeight: 'bold', textAlign: 'center' }}>
                {formik.values?.nameLeader}
              </Grid>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <>
            <Button variant="contained" color="secondary" onClick={handleCloseDialog}>
              Hy
            </Button>
            {item?.employee ? (
              <>
                <Button variant="contained" color="primary" onClick={handlePrint}>
                  In
                </Button>
              </>
            ) : (
              <Button type="submit" variant="contained" color="primary">
                To hp ng
              </Button>
            )}
          </>
        </DialogActions>
      </form>
    </Dialog>
  );
}
